import React from 'react'
import { Link } from 'gatsby'
import {Badge, Card, CardBody, CardText, CardSubtitle, CardTitle} from 'reactstrap'
import Img from 'gatsby-image'
import { slugify } from '../util/utilityfunctions'

const Post = ({title, slug, date, body, fluid, tags }) => {
    return(

        <Card id="yan">
            <Img className="card-img"  fluid={fluid} />
            <CardBody>
                <CardTitle >
                  <Link className="title" to={slug}>{title}</Link>
                </CardTitle>
                <CardSubtitle>
                    <span className="text-info">{date}</span>{' '}
                </CardSubtitle>
                <CardText>{body}</CardText>
                <ul className="post-tags">
                    {tags.map(tag => (
                        <li key={tag}>
                            <Link to={`/tag/${slugify(tag)}`}>
                    <Badge className="badge" pill >{tag}</Badge>
                            </Link>
                        </li>
                    ))}
                </ul>
                <Link to={slug} className="btn float-right buttonColor">Daha Fazla Oku</Link>
            </CardBody>
        </Card>

    )
}

export default Post